.header-section{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    padding: 10px 50px;
}
.logo{
    height: 80px;
}
.call-info{
    font-weight: 700;
    font-size: 22px;
}
.sub-info{
    display: block;
    font-size: 14px;
}
.header-description{
    font-size: 12px;
}
.header-border{
    border: 3px solid  #1680a7;;
}


@media screen and (max-width:750px) {
    .header-section{
        padding: 15px 10px;
    }
    .logo{
        padding-right: 50px;
    }
    .call-info{
        font-size: 14px;
    }
    .sub-info{
        font-size: 11px;
    }
    .header-description{
        font-size: 8px;
    }
}