@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    list-style: none;
    font-family: 'Poppins', sans-serif;
}
.pointer{
    cursor: pointer;
}
.header-bg{
    background: url(/static/media/hero.d71bb30a.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
    height: 100vh;
    width: 100%;
}
.header-section{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    padding: 10px 50px;
}
.logo{
    height: 80px;
}
.call-info{
    font-weight: 700;
    font-size: 22px;
}
.sub-info{
    display: block;
    font-size: 14px;
}
.header-description{
    font-size: 12px;
}
.header-border{
    border: 3px solid  #1680a7;;
}


@media screen and (max-width:750px) {
    .header-section{
        padding: 15px 10px;
    }
    .logo{
        padding-right: 50px;
    }
    .call-info{
        font-size: 14px;
    }
    .sub-info{
        font-size: 11px;
    }
    .header-description{
        font-size: 8px;
    }
}
.bar {
  width: 100%;
  background-color: #1680a7;
  position: relative;
  top: 0px!important;
  height: 5px;
}
.bar div {
  height: 5px;
  background-color: #ace4fa !important;
  transition: all 0.4s linear;
}

#hero {
  min-height: 460px;
  display: flex;
  padding-bottom: 40px;
}
.ques h1 {
  text-align: center;
  color: #fff;
  text-shadow: 1px 2px 5px #000000ab;
  margin-bottom: 25px;
}
.ques {
  padding: 0 20px;
  min-height: 450px;
}
.parent {
  overflow: hidden;
  max-width: 1000px;
  margin: auto;
  scroll-behavior: smooth;
}
.slider {
  display: grid;
  grid-template-columns: repeat(3, 1000px);
  transition: all 0.7s linear;
}
#go1 {
  display: grid;
  grid-template-columns: repeat(4, 1000px);
}
#go2 {
  display: grid;
  grid-template-columns: repeat(2, 1000px);
}
.call {
  font-size: 24px;
  line-height: 28px;
}

.error {
  color: #e91a1a;
  background: #fff;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0px 8px;
  font-weight: 600;
  margin: auto;
  position: relative;
}
.error::after {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  border: 6px solid transparent;
  border-bottom-color: #fff;
}
.btns {
  border: 2px solid #fff;
  padding: 5px 25px;
  border-radius: 6px;
  font-weight: 500;
  background: #eca305;
  color: #fff;
  display: flex;
  align-items: center;
  grid-gap: 6px;
  gap: 6px;
  transition: all 0.3s ease;
  font-size: 21px;
}
.btns:disabled {
  opacity: 0.5;
}
.btns:disabled:hover {
  background: #eca305;
}
.strong_scroll {
  scroll-behavior: auto !important;
}
.conseq {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  justify-content: center;
  margin-top: 20px;
}
.btns:hover {
  background: #e28800;
}
.btns2 {
  background: #fff;
  color: #1b7eaf;
  font-weight: bold;
}
.btns2:hover {
  background: rgb(236, 236, 236);
  color: #1b7eaf;
}
.options {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr;
  max-width: 600px;
  margin: auto;
  text-align: center;
  grid-gap: 20px;
  gap: 20px;
}
.options > div {
  position: relative;
  transition: all 0.2s ease;
}
.options > div .hole {
  position: absolute;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #ddd;
  right: 5px;
  top: 5px;
  transition: all 0.2s ease;
  pointer-events: none;
}
.welcome {
  position: absolute;
  width: 100%;
  height: 80%;
  left: 0%;
  top: 15%;
  z-index: 5;
  display: grid;
  place-items: center;
  text-align: center;
  background: #fff;
  padding: 20px;
  -webkit-animation: fade 1s ease;
          animation: fade 1s ease;
}

@-webkit-keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
