.checkMe {
  background-image: url(data:image/png;base64,)!important;
  background-repeat: no-repeat !important;
  background-size: 17px !important;
  background-position: center !important;
  background-color: #fbad04 !important;
}

.form-control:focus {
  box-shadow: 0px 0px 1px 3px rgb(235, 212, 11);
  border: 0;
}
.form-control {
  outline: none;
  background: #f5f5f5;
}
.options label {
  background: #fff;
  padding: 25px;
  border-radius: 9px;
  cursor: pointer;
  display: block;
  font-weight: 700;
  font-size: 18px;
  color: #3e3e3e;
  height: 100%;
  transition: all 0.3s ease;
}
.options label:hover {
  background: rgb(238, 238, 238);
}
.options label * {
  pointer-events: none;
}
#ques1 .options label:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
#ques1 label img {
  width: 100%;
  height: 90px;
  object-fit: contain;
  margin-bottom: 10px;
  object-position: bottom;
}
#ques1 label img {
  height: 90px;
  margin-bottom: 10px;
}
.options > div:not(#ques1 .options > div):hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.options > div p {
  font-size: 18px;
  font-weight: 500;
  color: rgb(54, 54, 54);
}
#hero .form {
  max-width: 540px;
  margin: auto;
  /* font-family: var(--poppins); */
}
.same_day_form {
  display: grid;
  grid-gap: 8px;
  gap: 8px;
}
.ques1_main {
  grid-template-columns: 2fr 1.5fr;
}
.same_day_form input {
  width: 100%;
  /* font-family: var(--poppins); */
  border: 0;
  padding: 16px 23px;
  border-radius: 33px;
  background: #ffffffb3;
}
.same_day_form input:focus {
  background: #fff;
}
.same_day_form button {
  border-radius: 33px;
  border-width: 5px;
  margin-top: 10px;
}
.same_day_form input::-webkit-input-placeholder {
  font-weight: 600;
  color: rgb(68, 68, 68);
}
.same_day_form input:-ms-input-placeholder {
  font-weight: 600;
  color: rgb(68, 68, 68);
}
.same_day_form input::placeholder {
  font-weight: 600;
  color: rgb(68, 68, 68);
}
.submit,
.submit:disabled:hover {
  background: #ff8731;
}
.submit:hover {
  background: #df6812;
}
.bar div {
  height: 5px;
  width: 0;
  background-color: #ace4fa;
  transition: all 0.4s linear;
}
.bar {
  width: 100%;
  background-color: #1680a7;
  position: relative;
  top: -15px;
  height: 5px;
}
#video .p-2 {
  max-width: 700px;
  margin-top: 30px;
  padding: 0 !important;
}
#advantage h2 {
  font-weight: 600;
  position: relative;
  margin-bottom: 15px;
}
.mb_20 {
  margin-bottom: 4.5rem !important;
}
.box {
  padding: 40px 25px;
  border: 2px solid #999999;
  border-radius: 30px;
  transition: all 0.3s ease;
  display: flex;
  grid-gap: 15px;
  gap: 15px;
}
.box h3 {
  color: #eca305;
  transition: all 0.3s ease;
  margin-bottom: 7px;
  font-size: 22px;
}
.box:hover h3 {
  color: #fff;
}
.box img {
  height: 100px;
  object-fit: contain;
  margin-bottom: 6px;
}
.box:hover {
  background: #2a8ff4;
  color: #fff;
  border-color: #2a8ff4;
}
#advantage p {
  color: rgb(87, 87, 87);
  font-weight: 400;
  transition: all 0.3s ease;
}
.box:hover p {
  color: #fff !important;
}
.span3rd {
  grid-column: 1/3;
  width: 70%;
  margin: auto;
}
footer {
  background: #404343;
  padding: 40px 25px;
}
.h11 {
  padding: 15px 30px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  background: #1d9bce;
  color: #fff;
