}
#quote {
  padding: 55px 30px;
  /* background: url("./img/quote.jpg") center center no-repeat; */
  background-size: cover;
  color: #fff;
}
#writing li,
#depo li {
  font-size: 18px;
  /* font-family: var(--poppins); */
  font-weight: 500;
}
#depo li {
