}
#depo h1 {
  /* font-family: var(--colored); */
  color: #f55343;
}

#writing {
  /* background: url("./img/writing_bg1.png") 0% no-repeat, */
  /* url("./img/writing_bg2.png") 100% no-repeat; */
  background-size: 145px, 169px;
}

.check__ ~ label {
  height: 25px;
  width: 25px;
  display: grid;
  place-items: center;
  background: #fff;
  transition: all 0.3s ease;
  border-radius: 5px;
  cursor: pointer;
}
.check__:checked ~ label {
  background: #ec820e;
}
.curve_text img {
  width: 40px;
  position: absolute;
  right: -45px;
  bottom: 25px;
}
.h12 {
  text-transform: uppercase;
  max-width: 1000px;
  margin: auto;
  padding: 0 15px;
}
.curve_text {
  color: #fff;
  line-height: 18px;
  margin-top: 22px;
  position: relative;
  /* font-family: var(--poppins); */
}
.toasts {
  position: fixed;
  background: #eca305;
  right: 20px;
  bottom: 30px;
  border-radius: 33px;
  padding: 15px 25px;
  -webkit-animation: come 0.5s linear forwards;
          animation: come 0.5s linear forwards;
  box-shadow: 0px 0px 6px 0px #00000063;
  z-index: 3;
}

.same_day_form input[type="number"]::-webkit-inner-spin-button,
.same_day_form input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

@-webkit-keyframes come {
  from {
    -webkit-transform: translateX(40px);
            transform: translateX(40px);
    opacity: 0;
  }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1;
  }
}

@keyframes come {
  from {
    -webkit-transform: translateX(40px);
            transform: translateX(40px);
    opacity: 0;
  }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1;
  }
}
@-webkit-keyframes go {
  from {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1;
  }
  to {
    -webkit-transform: translateX(-40px);
            transform: translateX(-40px);
    opacity: 0;
  }
}
@keyframes go {
  from {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1;
  }
  to {
    -webkit-transform: translateX(-40px);
            transform: translateX(-40px);
    opacity: 0;
  }
}

