.box-section{
    margin: 80px 0px;
}
.box-card{
    border: 2px solid #999;
    border-radius: 30px;
    display: flex;
    gap: 15px;
    padding: 40px 25px;
    transition: all .3s ease;
}
.box-card:hover{
    background: #2a8ff4;
    border-color: #2a8ff4;
    color: #fff !important;
}
.box-image{
    height: 100px;
    margin-bottom: 6px;
    object-fit: contain;
}
.box-title{
    color: #eca305;
    font-size: 22px;
    margin-bottom: 7px;
    transition: all .3s ease;
    text-align: center;
 font-family: "Russo One", sans-serif;
}
.box-description{
    color: #575757;
    font-weight: 400;
    transition: all .3s ease;
    font-size: .875rem;
    text-align: center;
    font-family: "Russo One", sans-serif;
}

@media screen and (max-width:1000px) {
  .box-card{ 
       flex-direction: column;
    }
}