.secret-section{
    margin: 80px 0px;
    background: url(../../assets/images/writing_bg1.png) 0 no-repeat,url(../../assets//images/writing_bg2.png) 100% no-repeat;
    background-size: 145px,169px;
}
.secret-title{
    background: #1d9bce;
    border-radius: 33px;
    color: #fff;
    margin: auto;
    padding: 15px 30px;
    text-align: center;
    width: fit-content;
}

@media screen and (max-width:700px) {
    .secret-section{
        background: none;
    }
}