.deposit-section{
    margin: 80px 0px;
    background: url(../../assets/images/pencil-hand.png)no-repeat 0,url(data:image/png;base64,) no-repeat 100% 0;
}
.grid{
    display: grid;
}
.w-fit{
    width: fit-content;
}
.gap-3 {
    gap: 1rem!important;
}
.grid li{
    list-style: none;
    display: flex;
    gap: 10px;
    font-size: 18px;
    font-weight: 500;
}
.deposit-title{
    font-size: 2.5rem;
    font-weight: 700;
    text-align: center;
   color: #f55343;
}

@media screen and (max-width:700px) {
    .deposit-section{
        background: none;
    }
    .deposit-title{
        font-size: 1.8rem;
    }
}