.video-section{
    background: url(../../img/hand.png) no-repeat 100% 100%;
    background-size: 220px;
}
.video-title{
    margin: auto;
    max-width: 1000px;
    padding: 0 15px;
    text-transform: uppercase;
    position: relative;
    font-size: 2.5rem;
    font-weight: 500;
    font-family: "Russo One", sans-serif;
}
.arrow{
    position: absolute;
    right: -110px;
    top: 0;
    width: 200px;
}
.video{
    display: flex;
    margin: 0 auto;
    max-width: 700px;
}

@media screen and (max-width:1150px){
     .arrow {
        width: 150px;
        right: -105px;
    }
    .video-title{
        max-width: 800px;
        width: 77%;
    }
}

@media screen and (max-width:1000px){
    .arrow {
      display: none;
   }
   .video-section{
       background-image: none;
   }
}
@media screen and (max-width:572px){
   .video-title{
       font-size: 1.2rem;
   }
}