.quote-section{
    background: url(../../assets/images/quote.jpg) ;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 55px 30px
}
.quote-title{
    font-size: 28px;
    color: #fff;
    font-family: "Russo One", sans-serif;
    font-weight: 500;
}
.quote-subtitle{
    font-size: 16px;
    color: #fff;
    font-family: "Russo One", sans-serif;
    font-weight: 500;
}
.quote-button{
    padding: 10px 40px;
    background: #fff;
    color: #1b7eaf;
    font-weight: 700;
    font-size: 21px;
    border-radius: 6px;
    border: 0;
    box-shadow: 0 2px 3px 0 #62626282;
    text-decoration: none;
    font-family: "Russo One", sans-serif;
}